  background: rgba(255, 255, 255, 0.1);
  border: none;
  border-radius: 50%;
  color: rgba(255, 255, 255, 0.7);
  cursor: pointer;
  transition: all 0.2s ease;
  font-size: 0.9rem;
}
.search-clear:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: scale(1.1);
}
.filter-panel {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  padding: 1.5rem;
  background: var(--glass-bg);
  border: 1px solid var(--glass-border);
  border-radius: 12px;
  margin-top: 1rem;
}
.filter-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  flex: 1;
  min-width: 200px;
}
.filter-label {
  font-size: 0.875rem;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.8);
}
.filter-select {
  padding: 0.625rem 1rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid var(--glass-border);
  border-radius: 8px;
  color: var(--foreground);
  font-size: 0.95rem;
  cursor: pointer;
  transition: all 0.2s ease;
}
.filter-select:hover {
  background: rgba(255, 255, 255, 0.08);
  border-color: var(--primary);
}
.filter-select:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 3px rgba(139, 92, 246, 0.1);
}
.clear-filters-button {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.625rem 1.25rem;
  background: rgba(239, 68, 68, 0.1);
  border: 1px solid rgba(239, 68, 68, 0.3);
  border-radius: 8px;
  color: #ef4444;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  align-self: flex-end;
}
.clear-filters-button:hover {
  background: rgba(239, 68, 68, 0.2);
  transform: translateY(-2px);
}
.search-filter-section {
  margin-bottom: 2rem;
}
.favorites-page {
  min-height: 100vh;
  padding: 2rem;
  animation: fadeIn 0.6s ease-out;
}
.favorites-container {
  max-width: 1200px;
  margin: 0 auto;
}
.favorites-header {
  text-align: center;
  margin-bottom: 3rem;
  animation: fadeIn 0.8s ease-out;
}
.favorites-content {
  animation: fadeIn 0.8s ease-out 0.2s both;
}
.favorites-count {
  text-align: center;
  font-size: 1.1rem;
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: 2rem;
  font-weight: 600;
}
.profile-page {
  min-height: 100vh;
  padding: 2rem;
  animation: fadeIn 0.6s ease-out;
}
.profile-container {
  max-width: 1200px;
  margin: 0 auto;
}
.profile-header {
  display: flex;
  align-items: center;
  gap: 2rem;
  padding: 2rem;
  background: var(--glass-bg);
  border: 1px solid var(--glass-border);
  border-radius: 16px;
  margin-bottom: 2rem;
}
.profile-avatar {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  overflow: hidden;
  border: 3px solid var(--primary);
  flex-shrink: 0;
}
.profile-avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.avatar-placeholder {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, var(--primary), var(--secondary));
  font-size: 3rem;
}
.profile-info {
  flex: 1;
}
.profile-name {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
  background: linear-gradient(135deg, var(--primary), var(--secondary));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
.profile-badge {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  background: var(--glass-bg);
  border: 1px solid var(--glass-border);
  border-radius: 20px;
  font-weight: 600;
}
.badge-emoji {
  font-size: 1.5rem;
}
.badge-name {
  font-size: 1rem;
}
.profile-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
  margin-bottom: 2rem;
}
.stat-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  padding: 1.5rem;
  background: var(--glass-bg);
  border: 1px solid var(--glass-border);
  border-radius: 12px;
  transition: all 0.3s ease;
}
.stat-card:hover {
  transform: translateY(-4px);
  box-shadow: var(--shadow-md);
}
.stat-icon {
  font-size: 2rem;
}
.stat-value {
  font-size: 2rem;
  font-weight: 700;
  background: linear-gradient(135deg, var(--primary), var(--secondary));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
.stat-label {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.7);
}
.badge-showcase {
  margin-bottom: 3rem;
}
.section-title {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
  text-align: center;
}
.badges-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(140px, 1fr));
  gap: 1rem;
}
.badge-item {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  padding: 1.5rem 1rem;
  background: var(--glass-bg);
  border: 1px solid var(--glass-border);
  border-radius: 12px;
  transition: all 0.3s ease;
}
.badge-item.unlocked {
  border-color: var(--primary);
}
.badge-item.unlocked:hover {
  transform: translateY(-4px);
  box-shadow: var(--shadow-md);
}
.badge-item.locked {
  opacity: 0.4;
  filter: grayscale(1);
}
.badge-item .badge-emoji {
  font-size: 2.5rem;
}
.badge-item .badge-name {
  font-size: 0.85rem;
  text-align: center;
  font-weight: 600;
}
.badge-lock {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  font-size: 1rem;
}
.user-stories-section {
  margin-top: 3rem;
}
.story-card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header-actions {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}
@media (max-width: 768px) {
  .search-filter-section {
    padding: 0 1rem;
  }
  .filter-panel {
    flex-direction: column;
  }
  .filter-group {
    min-width: 100%;
  }
  .profile-header {
    flex-direction: column;
    text-align: center;
  }
  .profile-avatar {
    width: 100px;
    height: 100px;
  }
  .profile-name {
    font-size: 1.5rem;
  }
  .profile-stats {
    grid-template-columns: repeat(2, 1fr);
  }
  .badges-grid {
    grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  }
}
